<template>
	<b-carousel
		id="main-carousel"
		v-model="slide"
		:interval="4000"
		controls
		indicators
		img-width="1024"
		img-height="480"
		@sliding-start="onSlideStart"
		@sliding-end="onSlideEnd"
		class="mt-4"
	>
		<b-carousel-slide img-src="/images/carousel/carousel-item-3.jpg"></b-carousel-slide>
		<b-carousel-slide img-src="/images/carousel/carousel-item-1.jpg"></b-carousel-slide>
	</b-carousel>
</template>

<script>
export default {
	name: "MainCarousel",
	data() {
		return {
			slide: 0,
			sliding: null,
		};
	},
	methods: {
		onSlideStart(slide) {
			this.sliding = true;
		},
		onSlideEnd(slide) {
			this.sliding = false;
		},
	},
};
</script>

