import Vue from "vue";
import VueRouter from "vue-router";

import Home from "./pages/Home.vue";
import Order from "./pages/Order.vue";

Vue.use(VueRouter);

const router = new VueRouter({
    mode: "history",
    linkExactActiveClass: "active",
    routes: [
        {
            path: "/",
            name: "home",
            component: Home
        },
        {
            path: "/order",
            name: "order",
            component: Order
        }
    ]
});

export default router;